// 初始化
$(function() {
    onProductsHover()
    onFeatureHover()
    onBasicHover()
})

// 监听产品hover状态
function onProductsHover() {
    $('.product__products-item').on('mouseover click', function() {
      if(!$(this).hasClass('hover')) {
        resetProductsHover()
        $(this).addClass('hover')
        var icon = $(this).children('.product__products-icon')
        icon.attr('src', icon.data('hover'))
        changeAttach($(this))
      }
    })
}

// 重置产品hover状态
function resetProductsHover() {
    $('.product__products-item').removeClass('hover')
    $('.product__products-icon').each(function() {
      $(this).attr('src', $(this).data('normal'))
    })
}

// 监听特色hover状态
function onFeatureHover() {
    $('.product__feature-item').on('mouseover click', function() {
      if(!$(this).hasClass('hover')) {
        resetFeatureHover()
        $(this).addClass('hover')
        changeAttach($(this))
      }
    })
}

  // 重置特色hover状态
function resetFeatureHover() {
    $('.product__feature-item').removeClass('hover')
}

// 监听定制家居特性模块hover状态
function onBasicHover() {
  $('.product__basic-item').on('mouseover click', function() {
    if(!$(this).hasClass('hover')) {
      resetBasicHover()
      $(this).addClass('hover')
      changeAttach($(this))
    }
  })
}

// 重置定制家居特性模块hover状态
function resetBasicHover() {
  $('.product__basic-item').removeClass('hover')
}

// 切换视频/图片资源
function changeAttach(item) {
  var parentClass = item.prop('className').split(' ')[0].split('-')[0]
  $('.' + parentClass + '-media').hide()
  $('.' + parentClass + '-media').each(function() {
    if($(this).data('index') === item.data('index')) {
      $(this).show()
    }
  })
}
